



























import Vue from 'vue';

export default Vue.extend({
  name: "DataBindingTest",
  data: () => ({
    lists: [
      {title: 'ToDoタイトル１つ目', body: 'ToDo中身１つ目'},
      {title: 'ToDoタイトル２つ目', body: 'ToDo中身２つ目'}
    ],
    title: '',
    body: ''
  }),
  methods: {
    // Todoの追加
    addList: function(){

      this.lists.push({title: this.title, body: this.body})
      this.title = ''
      this.body = ''
    }
  }
});
